.footer {
    height: 89px;
    background: $color-light;
    box-shadow: 0 -5px 40px $color-black-shadow;
}

.footer .container {
    height: 100%;
    @include flex--space-between;
}

.footer-logo {
    @include raleway;
    font-weight: 300;
    font-size: 26px;
    line-height: 31px;
    color: $color-blue;
}

.footer-img:first-of-type {
    margin-right: 20px;
}

.small-text {
    font-size: 11px;
    line-height: 15px;
    color: $color-grey-light;
}
