.contact-form {
    height: 590px;
    padding: 159px 0 59px ;
}

.contact-form-container {
    height: 372px;
    max-width: 896px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.contact-form-left {
    width: 50%;
}

.contact-form-title {
    @include raleway;
    font-size: 49px;
    line-height: 59px;
    padding-bottom: 22px;
}

.contact-form-text {
    padding-bottom: 40px;
}

.contact-small-text {
    font-weight: 600;
    line-height: 22px;
    color: $color-grey-light;
}

.contact-small-text:nth-child(2) {
    margin-right: 24px;
}

.contact-form-content {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.contact-image {
    display: block;
    margin-right: 10px;
}

.contact-form-right-content {
    width: 440px;
    height: 100%;
    position: relative;
}

.contact-form-right-content label[for=name],
.contact-form-right-content label[for=email] {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 11px;
    line-height: 15px;
    letter-spacing: 3.28px;
    color: $color-blue;
    margin-top: 30px;
}

.contact-form-right-content input[type=text],
.contact-form-right-content input[type=email]  {
    display: block;
    width: 100%;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid $color-grey-second;
    font-size: 26px;
    @include raleway;
}

input[type=checkbox] {
    display: none;
}

label[for=checkBox] {
    font-size: 12px;
    line-height: 17px;
    color: $color-grey-dark;
    max-width: 439px;
    float: left;
    padding-left: 34px;
    cursor: pointer;
    position: relative;
    margin-top: 30px;
}

input[type=checkbox] + label[for=checkBox]:before{
    position: absolute;
    left: 0;
    top: 0;
    content: '';
    display: block;
    background: $color-white 0 0 no-repeat padding-box;
    border: 1px solid $color-grey-second;
    border-radius: 4px;
    opacity: 1;
    width: 24px;
    height: 24px;
}

#checkBox:checked ~ label[for=checkBox]:before {
    background: $color-white no-repeat center center url("../assets/Checkmark.svg");
}

.button {
    width: 226px;
    height: 57px;
    background: $color-blue;
    box-shadow: 5px 10px 14px $color-blue-shadow;
    border: none;
    border-radius: 35px;
    position: absolute;
    bottom: 0;
    right: 0;
    font-weight: 700;
    font-size: 13px;
    line-height: 18px;
    color: $color-white;
    cursor: pointer;
    outline: none;
}

.button:hover {
    background-color: darken($color: $color-blue, $amount: 10);
    box-shadow: 5px 10px 15px darken($color: $color-blue, $amount: 10);
}
form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
}


@media (max-width: 1000px) {
    .contact-form {
        height: auto;
        padding: 50px 20px;
        .contact-form-container {
            width: 100%;
            height: auto;
            padding: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .contact-form-left {
                width: 100%;
                text-align: center;
                // height: auto;
            }
            .contact-form-content {
                justify-content: center;
                flex-wrap: wrap;
            }
            .contact-form-right {
                width: 100%;
            }
            .contact-form-right-content {
                width: 100%;
                height: 100%;
                position: relative;

            }
            .button {
                display: inline-block
            }
        }
    }
}

@media (max-width: 750px) {
    .contact-form-right {
        height: 375px;
    }
}

@media (max-width: 450px) {
    .contact-form-right {
        height: 450px;
    }
}