.banner {
    height: 100vh;
    width: 100%;
}

.banner-top {
    height: 50%;
    @include flex--center;
    flex-direction: column;
}

.banner-bottom {
    height: 50%;
    background-image: url('../assets/Background.png');
    background-size: contain;
    background-position: bottom ;
    background-repeat: no-repeat;
}

.banner-title {
    font-size: 49px;
    line-height: 59px;
    @include raleway;
    padding-top: 208px;
    padding-bottom: 20px;
}

.banner-text {
    line-height: 22px;
}

.banner-btn {
    display: block;
    width: 440px;
    height: 67px;
    font-weight: 700;
    line-height: 67px;
    color: $color-white;
    background: $color-blue;
    box-shadow: 5px 10px 14px $color-blue-shadow;
    border-radius: 40px;
    opacity: 1;
    border: 0;
    outline: none;
    text-align: center;
    margin-top: 38px;
}

.banner-btn:hover  {
    background-color: darken($color: $color-blue, $amount: 10);
    box-shadow: 5px 10px 15px darken($color: $color-blue, $amount: 10);
}


@media (max-width: 1366px) {
    .banner-title {
        font-size: 35px;
        line-height: 42px;
        // padding-top: 85px;
        font-weight: 400;
    }
    .banner-text {
        width: 300px;
        text-align: center;
    }
    .banner-btn {
       width: 310px;
       margin-top: 28px;
    }
    .banner-bottom {
        background-size: cover;
        background-position: top center;
    }
}
