.popular {
    height: 531px;
    
    .container {
        max-width: 822px;
        height: 100%;
        @include flex--center;
        flex-direction: column;
    }
}
    
.popular-title {
    font-size: 36px;
    line-height: 43px;
    @include raleway;
}

.popular-items-wrapper {
    height: 60%;
    width: 100%;
    @include flex--space-between;
}

.popular-item {
    height: 100%;
    @include flex--center;
    flex-direction: column;
    text-align: center;
}

.popular-item-title {
    @include raleway;
    font-size: 20px;
    line-height: 24px;
    font-weight: 800;
    letter-spacing: 5.96px;
    padding: 18px 0 22px;
}

.popular-item-text {
    @include raleway;
    line-height: 19px;
    color: $color-grey-light;
}
.popular-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(0, -50%);
}

.popular-img-eye {
    margin-left: -25%
}

.popular-circle {
    width: 63px;
    height: 63px;
    border-radius: 50%;
    position: relative;
}

.circle-person {
    background: $color-pink;
}

.circle-eye {
    background: $color-blue;
}

.circle-assets {
    background: $color-green;
}

.cheat {
    color: $color-white;
}

@media (max-width: 850px) {
    .popular {
        height: auto;
        margin-top: 50px;
    }
    .popular-title {
        font-size: 30px;
        width: 300px;
        text-align: center;
        padding-bottom: 50px;
    }
    .popular-items-wrapper {
        flex-direction: column;
    }
    .popular-item {
        margin-bottom: 20px;
    }
}