.pricing {
    width: 100%;
    height: 610px;
    margin-top: 106px;
}

.pricing-container {
    max-width: 1266px;
    height: 100%;
    margin: 0 auto;
}

.pricing-title {
    @include raleway;
    font-size: 36px;
    height: 86px;
    text-align: center;
}

.pricing-wrapper {
    @include flex--space-between;
}

.price-flex {
    @include flex--space-between;
    flex-direction: column;
}

.basic-price, .premium-price {
    width: 372px;
    height: 461px;
    border: 1px solid $color-pink;
    border-radius: 18px;
    position: relative;
}

.professional-price {
    width: 405px;
    height: 500px;
    border: 1px solid $color-blue-second;
    border-radius: 18px;
    position: relative;
    box-shadow: 5px 10px 27px $color-blue-shadow;
}

.premium-price {
    border: 1px solid $color-green;
}

.price-btn {
    width: 150px;
    height: 50px;
    border-radius: 23px;
    position: absolute;
    bottom: 0;
    margin-bottom: -25px;
    text-align: center;
    color: $color-white;
    font-size: 13px;
    font-weight: 700;
    line-height: 50px;
    outline: none;
}

.basic-btn {
    background-color: $color-pink;

}

.basic-btn:hover {
    background-color: darken($color: $color-pink, $amount: 10);
    box-shadow: 5px 10px 14px darken($color: $color-pink, $amount: 10);
}

.professional-btn {
    background-color: $color-blue-second;
}

.professional-btn:hover {
    background-color: darken($color: $color-blue-second, $amount: 10);
    box-shadow: 5px 10px 14px darken($color: $color-blue-second, $amount: 10);
}

.premium-btn {
    background-color: $color-green;
}

.premium-btn:hover {
    background-color: darken($color: $color-green, $amount: 10);
    box-shadow: 5px 10px 14px darken($color: $color-green, $amount: 10);

}

.price-top {
    text-align: center;
}

.price-title {
    @include raleway;
    font-weight: 700;
    font-size: 20px;
    color: $color-blue-shadow-second;
    padding: 38px 0 14px;
}

.professional-price .price-title {
    font-size: 21px;
}

.price-span {
    font-size: 46px;
    font-weight: 300;
    padding-bottom: 7px;
}

.basic-price .price-span {
    color: $color-pink;
}

.professional-price .price-span {
    color: $color-blue-second;
}

.premium-price .price-span {
    color: $color-green;
}

.limited-offer {
    @include raleway;
    font-weight: 700;
    font-size: 12px;
    color: $color-blue-shadow-second;
}

.price-list {
    font-size: 14px;
    line-height: 41px;
    margin-bottom: 55px;
    @include flex--center;
    flex-direction: column;
    list-style: none;
}

.professional-price .price-list {
    line-height: 46px;
}

.price-list-item:first-child {
    text-transform: uppercase;
}

.price-list-item {
    position: relative;
}

.price-list-item:before {
    content: '';
    background: url("../assets/Ckechmark-1.svg");
    font-size: 10px;
    line-height: 11px;
    text-align: center;
    margin-right: 10px;
    width: 11px;
    height: 11px;
    position: absolute;
    left: -24px;
    top: 50%;
    margin-top: -6px;
}

.basic-price .price-list-item:nth-child(4):before, .basic-price .price-list-item:nth-child(5):before, .professional-price .price-list-item:nth-child(5):before   {
    content: '';
    background: url("../assets/Close.svg");
}


@media (max-width: 1000px) {
    .pricing {
        height: auto;
    }
    .pricing-wrapper {
        display: flex;
        flex-direction: column;
    }
    .price-flex {
        margin-bottom: 50px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: row;
    }
    .basic-price, .premium-price {
       width: 60vw;
       height: 251px;
    }
    
    .professional-price {
       width: 70vw;
       height: 277px;
    }

    .price-list {
        align-items: flex-start;
        margin-bottom: 15px;
    }

    .price-title,
    .price-span {
        padding: 0;
    }
}


@media (max-width: 600px) {
    .basic-price, .premium-price {
        width: 293px;
        height: 251px;
     }
     
     .professional-price {
         width: 339px;
         height: 277px;
     }
}