.header {
  background: #ffffff;
  box-shadow: 0 3px 6px #00000029;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}

.header .navbar-list {
  display: flex;
}

.header .navbar li {
  padding-left: 94px;
}

.header .navbar li:first-child {
  margin-left: 0;
}

.header .navbar a {
  font-size: 11px;
  font-weight: 700;
  line-height: 70px;
  color: #A5A5A5;
  text-transform: uppercase;
  letter-spacing: 3.28px;
  display: block;
  position: relative;
  border-bottom: 1px solid transparent;
}

.header .navbar a:hover {
  color: #000000;
}

.header .navbar a::before {
  content: '';
  display: block;
  background: #0682b3;
  height: 1px;
  width: 0;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(0, 150%);
  transition: all .5s ease;
}

.header .navbar a:hover::before {
  width: calc(110%);
  left: -10%;
  transform: translate(0, 150%);
}

.header-logo {
  color: #08A6E4;
  font-size: 32px;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  text-align: left;
  letter-spacing: 0;
  opacity: 1;
}

.hamburger {
  padding: 10px;
  display: none;
  background-color: transparent;
  cursor: pointer;
  border: 0;
  margin: 0;
}

.hamburger__box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.hamburger__inner {
  width: 100%;
  height: 3px;
  background-color: #08A6E4;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: background-color .1s .2s ease-in-out;
}

.hamburger__inner::before,
.hamburger__inner::after {
  width: 100%;
  height: 3px;
  background-color: #08A6E4;
  position: absolute;
  content: '';
  left: 0;
  transition: transform .2s .2s ease-in-out;
}

.hamburger__inner::before {
  top: -10px;
}

.hamburger__inner::after {
  top: 10px;
}

.hamburger--active .hamburger__inner {
  background-color: transparent;
}

.hamburger--active .hamburger__inner::before {
  transform: translateY(10px) rotate(45deg);
}

.hamburger--active .hamburger__inner::after {
  transform: translateY(-10px) rotate(-45deg);
}

@media (max-width: 850px) {
  .navbar--active {
    width: 100%;
    position: absolute;
    top: 71px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
