.articles {
  width: 100%;
  min-height: 1395px;
  background: #F7F7F7;
}

.articles__container {
  max-width: 1124px;
  min-height: 1184px;
  margin: 0 auto;
  padding: 105px 0 0;
  background: #F7F7F7;
}

.article__text--box {
  height: 370px;
  max-width: 452px;
  margin-bottom: 37px;
  padding-left: 36px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.article__title {
  font-family: 'Raleway', sans-serif;
  font-size: 36px;
  line-height: 43px;
}

.article__text {
  line-height: 22px;
  padding: 30px 0 50px;
}

/* First article */
.article__first {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #ffffff;
  border: 1px solid #E8E9ED;
  margin-bottom: 37px;
}

.article__first--box {
  max-width: 433px;
  height: 294px;
  margin: 38px 71px 38px 0;
  position: relative;
}

.dash {
  width: 328px;
  border: 2px solid #FB3B64;
}

.article__first--box--rectangle {
  width: 180px;
  height: 180px;
  display: block;
  background: #FB3B64;
  border-radius: 41px;
  position: absolute;
  left: 50%;
  transform: translate(0, 20%) rotate(45deg);
}

.first-article-image-main {
  display: block;
  width: 386px;
  height: 219px;
  position: absolute;
  bottom: 0;
}

/* Second article */
.article__second {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #ffffff;
  border: 1px solid #E8E9ED;
  margin-bottom: 37px;
}

.article__second .article__text--box {
  max-width: 440px;
  padding-left: 0;
  margin: 0 auto;
}

.article__second-image-box {
  padding: 29px 0 13px 70px;
  position: relative;
}

.article__second-image-circle {
  display: block;
  position: absolute;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  background: #08A6E4;
}

.article__second--image {
  display: block;
  position: absolute;
  width: 426px;
  height: 328px;
  top: 10px;
}

.article-title-width {
  width: 250px;
}

.second-article-text {
  max-width: 436px;
  padding: 75px 0 71px;
  line-height: 22px;
}

.second-article-dash {
  border: 2px solid #08A6E4;
}

/* Third article*/
.article__third {
  margin-bottom: 105px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: none;
  background: #F7F7F7;
  width: 100%;
}

.third-article-first-part {
  width: 623px;
  height: 370px;
  display: inline-block;
  border: 1px solid #E8E9ED;
  background: #ffffff;
}

.third-article__text--box {
  max-width: 95%;
}

.article__third--text {
  max-width: 100%;
  padding: 75px 0 70px 0;
  line-height: 22px;
}

.third-article-second-part {
  width: 440px;
  height: 370px;
  border: 1px solid #E8E9ED;
  background: #55DFB4;
  position: relative;
}

.third-article-image {
  display: block;
  width: 296px;
  height: 208px;
  position: absolute;
  top: 98px;
  left: 62px;
}

.third-article-dash {
  border: 2px solid #55DFB4;
}

/* Media queries */
@media (max-width: 1000px) {
  .articles__container {
    padding: 30px;
  }
  .article__first,
  .article__second {
    height: auto;
    display: grid;
    grid-template-rows: 220px 220px;
    grid-template-columns: auto;
    margin-bottom: 30px;
  }
  .article__first .article__text--box,
  .article__second .article__text--box {
    grid-row: 2/3;
    align-self: center;
    justify-self: center;
    margin: 0;
    padding: 0 21px;
    width: 100%;
    height: 100%;
  }
  .article__first .article__text--box .article__title,
  .article__second .article__text--box .article__title {
    font-size: 30px;
    line-height: 36px;
  }
  .article__first .article__text--box .article__text,
  .article__first .article__text--box .second-article-text,
  .article__second .article__text--box .article__text,
  .article__second .article__text--box .second-article-text {
    font-size: 14px;
    line-height: 19px;
    padding: 17px 0 0;
  }
  .article__first .article__first--box,
  .article__first .article__second-image-box,
  .article__second .article__first--box,
  .article__second .article__second-image-box {
    grid-row: 1/2;
    align-self: center;
    justify-self: center;
    margin: 0;
    padding: 0;
    position: relative;
    height: 100%;
  }
  .article__first .article__first--box .first-article-image-main,
  .article__first .article__second-image-box .first-article-image-main,
  .article__second .article__first--box .first-article-image-main,
  .article__second .article__second-image-box .first-article-image-main {
    width: 220px;
    height: 110px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .article__first .article__first--box .article__first--box--rectangle,
  .article__first .article__second-image-box .article__first--box--rectangle,
  .article__second .article__first--box .article__first--box--rectangle,
  .article__second .article__second-image-box .article__first--box--rectangle {
    width: 90px;
    height: 90px;
    display: block;
    position: absolute;
    background: #FB3B64;
    border-radius: 41px;
    top: 50%;
    left: 50%;
    transform: translate(25%, -100%) rotate(45deg);
  }
  .article__first .dash,
  .article__second .dash {
    display: none;
  }
  .article__second .article__second-image-box {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .article__second .article__second-image-box .article__second-image-circle {
    display: block;
    position: absolute;
    width: 130px;
    height: 130px;
    border-radius: 50%;
    background: #08A6E4;
  }
  .article__second .article__second-image-box .article__second--image {
    display: block;
    position: absolute;
    width: 175px;
    height: 134px;
    top: 0;
    left: 0;
  }
  .article__third {
    margin-bottom: 105px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    border: none;
    background: #F7F7F7;
    width: 100%;
  }
  .third-article-first-part {
    width: 100%;
    height: auto;
    display: inline-block;
    border: 1px solid #E8E9ED;
    background: #ffffff;
  }
  .third-article-first-part .article__text--box {
    text-align: left;
    max-width: 452px;
    height: auto;
    margin: 0 auto;
    padding: 0 21px;
  }
  .third-article-first-part .article__text--box .article__title {
    font-size: 30px;
  }
  .third-article-first-part .article__text--box .article__third--text {
    padding-bottom: 10px;
  }
  .third-article-second-part {
    width: 100%;
    max-height: 220px;
    border: 1px solid #E8E9ED;
    background: #55DFB4;
    position: relative;
  }
  .third-article-image {
    display: block;
    max-width: 276px;
    max-height: 188px;
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .article__third--text {
    font-size: 14px;
    line-height: 19px;
    padding: 17px 0 0;
  }
  .third-article-dash {
    display: none;
  }
}

