  width: 440px;
  height: 100%;
  position: relative;
}

.contact-form-right-content label[for=name],
.contact-form-right-content label[for=email] {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 3.28px;
  color: #08A6E4;
  margin-top: 30px;
}

.contact-form-right-content input[type=text],
.contact-form-right-content input[type=email] {
  display: block;
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 1px solid #95989A;
  font-size: 26px;
  font-family: 'Raleway', sans-serif;
}

input[type=checkbox] {
  display: none;
}

label[for=checkBox] {
  font-size: 12px;
  line-height: 17px;
  color: #707070;
  max-width: 439px;
  float: left;
  padding-left: 34px;
  cursor: pointer;
  position: relative;
  margin-top: 30px;
}

input[type=checkbox] + label[for=checkBox]:before {
  position: absolute;
  left: 0;
  top: 0;
  content: '';
  display: block;
  background: #ffffff 0 0 no-repeat padding-box;
  border: 1px solid #95989A;
  border-radius: 4px;
  opacity: 1;
  width: 24px;
  height: 24px;
}

#checkBox:checked ~ label[for=checkBox]:before {
  background: #ffffff no-repeat center center url("../assets/Checkmark.svg");
}

.button {
  width: 226px;
  height: 57px;
  background: #08A6E4;
  box-shadow: 5px 10px 14px #07ACE629;
  border: none;
  border-radius: 35px;
  position: absolute;
  bottom: 0;
  right: 0;
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  color: #ffffff;
  cursor: pointer;
  outline: none;
}

.button:hover {
  background-color: #0682b3;
  box-shadow: 5px 10px 15px #0682b3;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 1000px) {
  .contact-form {
    height: auto;
    padding: 50px 20px;
  }
  .contact-form .contact-form-container {
    width: 100%;
    height: auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .contact-form .contact-form-container .contact-form-left {
    width: 100%;
    text-align: center;
  }
  .contact-form .contact-form-container .contact-form-content {
    justify-content: center;
    flex-wrap: wrap;
  }
  .contact-form .contact-form-container .contact-form-right {
    width: 100%;
  }
  .contact-form .contact-form-container .contact-form-right-content {
    width: 100%;
    height: 100%;
    position: relative;
  }
  .contact-form .contact-form-container .button {
    display: inline-block;
  }
}

@media (max-width: 750px) {
  .contact-form-right {
    height: 375px;
  }
}

@media (max-width: 450px) {
  .contact-form-right {
    height: 450px;
  }
}

.footer {
  height: 89px;
  background: #F7F7F7;
  box-shadow: 0 -5px 40px #00000029;
}

.footer .container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-logo {
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 26px;
  line-height: 31px;
  color: #08A6E4;
}

.footer-img:first-of-type {
  margin-right: 20px;
}

.small-text {
  font-size: 11px;
  line-height: 15px;
  color: #A5A5A5;
}

.body-wrapper {
  height: 100%;
}
